const $ = require('jquery');
require('slick-carousel');

class Reviews {
    constructor() {
        this.init();
    }

    init() {
        $(document).ready(function () {
            $('.reviews__carousel').slick({
                prevArrow: "<button type=\"button\" class=\"reviews__carousel-btn reviews__carousel-btn--left\"><img class=\"reviews__carousel-arrow\" src=\"../img/arrow.svg\" alt=\"Previous\"></button>",
                nextArrow: "<button type=\"button\" class=\"reviews__carousel-btn reviews__carousel-btn--right\"><img class=\"reviews__carousel-arrow reviews__carousel-arrow--left\" src=\"../img/arrow.svg\" alt=\"Next\"></button>",
                adaptiveHeight: true,
                fade: true,
                cssEase: 'ease-in-out',
                responsive: [
                    {
                        breakpoint: 1024,
                        settings: {
                            arrows: false,
                            dots: true,
                        }
                    }
                ]
            });
        });
    }
}

new Reviews();
